<template>
    <div>
        <el-row :gutter="20">
            <el-col :xs="24" :sm="24" :md="12" :lg="8" v-for="(item,index) in items" :key="index"><div class="project">
                <a :href='item.path' target="_blank"><img :src="item.imgpath" alt=""><div :id="item.id" class="shade"><p>{{ item.deraction }}</p></div></a>
                </div></el-col>
            <!-- <el-col :xs="24" :sm="24" :md="12" :lg="8"><div class="project">
                <a href='https://cnhekai.github.io/newspaper/' target="_blank"><img src="static/news.png" alt=""><div id="myNews" class="shade">显示文字</div></a>
                </div></el-col>
            <el-col :xs="24" :sm="24" :md="12" :lg="8"><div class="project">
                </div></el-col>
            <el-col :xs="24" :sm="24" :md="12" :lg="8"><div class="project">
                </div></el-col>
            <el-col :xs="24" :sm="24" :md="12" :lg="8"><div class="project">
                </div></el-col>
            <el-col :xs="24" :sm="24" :md="12" :lg="8"><div class="project">
                </div></el-col> -->
        </el-row>
    </div>
</template>

<script>
import myGame from '@/assets/2048.png'
import news from '@/assets/news.png'
import htmlBasics from '@/assets/html.png'
import vueSpa from '@/assets/vueSpa.png'
import Carousel from '@/assets/carousel.png'
import mobilePage from '@/assets/mobile.png'
export default{
    data(){
        return {
            items:[{
                path:'https://cnhekai.github.io/2048/',
                id:'myGame',
                imgpath:myGame,
                deraction:'这是一个使用jQuery编写的一个2048小游戏，使用键盘的方向键控制模块移动，也可以在手机端玩'
            },{
                path:'https://cnhekai.github.io/newspaper/',
                id:'myNews',
                imgpath:news,
                deraction:'这是使用HTML和CSS进行排版联系的页面'
            },{
                path:'https://cnhekai.github.io/htmlBasics/',
                id:'htmlBasics',
                imgpath:htmlBasics,
                deraction:'这是HTML和CSS的基础应用'
            },{
                path:'https://cnhekai.github.io/vueSpa/',
                id:'vueSpa',
                imgpath:vueSpa,
                deraction:'这是vue和vue-router练习页面'
            },{
                path:'https://cnhekai.github.io/3dCarousel/',
                id:'Carousel',
                imgpath:Carousel,
                deraction:'这是使用JS和CSS3的3d属性做的3D轮播图，可使用鼠标点击切换左右图片'
            },{
                path:'https://cnhekai.github.io/mobilePage/',
                id:'mobilePage',
                imgpath:mobilePage,
                deraction:'这是使用CSS媒体查询做的移动端页面，请使用手机模拟器或者手机打开该页面查看'
            }]
        }
    }
}
</script>

<style scoped>
    .el-col{
        margin-top: 10px;
        position: relative;
    }
    .project{
        padding-bottom: 100%;
        position: relative;
    }
    .project > a{
        width: 100%;
        height: 100%;
        position: absolute;
        display: block;
    }
    .project > a >img{
        width: 100%;
        height: 100%;
        display: block;
    }
    .shade{
        width: 80%;
        height: 70%;
        position: absolute;
        top:0;
        left: 0;
        text-align: center;
        background: rgba(0, 0, 0, 0.5);
        color: #fff;
        padding:30% 10% 0;
        display: block;
    }
    p{
        line-height: 1.2em;
        letter-spacing: 0.1em;
        text-align: justify;
        word-break: break-all
    }
    #myGame{
        opacity: 0;
    }
    #myGame:hover {
        opacity: 1;
    }
    #myNews{
        opacity: 0;
    }
    #myNews:hover {
        opacity: 1;
    }
    #htmlBasics{
        opacity: 0;
    }
    #htmlBasics:hover {
        opacity: 1;
    }
    #vueSpa{
        opacity: 0;
    }
    #vueSpa:hover {
        opacity: 1;
    }
    #Carousel{
        opacity: 0;
    }
    #Carousel:hover {
        opacity: 1;
    }
    #mobilePage{
        opacity: 0;
    }
    #mobilePage:hover {
        opacity: 1;
    }
</style>



