import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import aboutme from '@/components/aboutme'
import projects from '@/components/projects'
import contactme from '@/components/contactme'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect:'/home',
    },
    {
      path:'/home',
      name:'home',
      component:home
    },
    {
      path:'/aboutme',
      name:'aboutme',
      component:aboutme
    },{
      path:'/projects',
      name:'projects',
      component:projects
    },{
      path:'/contactme',
      name:'contactme',
      component:contactme
    }
  ]
})



