<template>
    <div>
        <div id="contactme">
            <div v-for="item of items"><span>{{item.title}}</span>{{item.content}}</div>
            <a target="_blank" href="http://mail.qq.com/cgi-bin/qm_share?t=qm_mailme&email=_svKyMrLw83Jzcy6i4vUmZWX" style="text-decoration:none;">
            <el-button type="primary">给我发QQ邮件</el-button></a>
        </div>
    </div>
</template>

<script>
    export default{
        data(){
            return {
                items:[{
                    title:'姓名：',
                    content:'何凯'
                },{
                    title:'性别：',
                    content:'男'
                },{
                    title:'电话：',
                    content:'17778395905'
                },{
                    title:'QQ邮箱：',
                    content:'1020197376@qq.com'
                },{
                    title:'OUTLOOK邮箱：',
                    content:'cnhekai@outlook.com'
                },]
            }
        }
    }
</script>

<style scoped>
#contactme{
    width: 94%;
    background: white;
    margin-top: 30px;
    padding: 3%;
    line-height: 30px;
    letter-spacing: 0.1em
}

span{
    display: inline-block;
    width: 30%;
    text-align: right;
    margin-right: 1%;
}

a{
    display: inline-block;
    margin-left: 25%;
    margin-top: 10px;
}
</style>



