<template>
  <div id="app">
      <el-container>
        <el-header :style="{'backgroundColor':'rgba(0,0,0,0.7)'}">Header</el-header>
        <el-container id="main">
          <el-main :style="{'overflow': 'scroll','overflow-x':'hidden'}">
            <div id='avatar' :class="[animation?'avatarAnimation':'avatarReverse']">
            <img :src='avatar' alt="">
            </div>
            <transition name='main' mode="out-in">
             <!-- @before-enter='beforeEnter'
             @enter='enter'
             @leave='leave'
             :class='false'> -->
              <router-view/>
            </transition> 
          </el-main>
          <el-aside style='width: 230px;'>
            <ul>
              <li v-for='(nav,index) in navs'  @click="isAvatarAnimation(index)">
                <router-link :to="{path:nav.path}" :class="{ 'navLink':true }" :style="{'background-color':nav.backgroundColor}">{{ nav.title }}</router-link>
              </li>
            </ul>
          </el-aside>
        </el-container>
        <el-footer :style="{'backgroundColor':'rgba(0,0,0,0.7)'}">Footer</el-footer>
      </el-container>
  </div>
</template>

<script>
import avatar from '@/assets/6.jpg'
export default {
  name: 'app',
  data(){
    return {
      animation:true,
      avatar:avatar,
      navs:[
        {
          title:'主页',
          backgroundColor:'#207d59',
          path:'/home'
        },{
          title:'自我介绍',
          backgroundColor:'#3b5b8e',
          path:'/aboutme'
        },{
          title:'项目',
          backgroundColor:'#5c9faf',
          path:'/projects'
        },{
          title:'联系我',
          backgroundColor:'#b85b53',
          path:'/contactme'
        }
      ]
    }
  },
  methods:{
    isAvatarAnimation:function(index){
      console.log(index)
      switch (index){
        case 0:
          this.animation=true
          //document.getElementById('avatar').className='avatarAnimation';
          break;
        case 1:
          this.animation=false
          //document.getElementById('avatar').className='avatarReverse';
          break;
        case 2:
          this.animation=false
          //document.getElementById('avatar').className='avatarReverse';
          break;
        case 3:
          this.animation=false
          //document.getElementById('avatar').className='avatarReverse';
          break;
      }
     
    },
    
    // beforeEnter:function(el){
    //   console.log(el)
      
    // },
    // enter:function(el,done){
    //   el.style.overflow="hidden";
    //   el.style.transition="all 1s"
    //   window.setTimeout(function(){el.style.height=el.offsetHeight+'px';console.log('进入时的'+el.offsetHeight)},100) 
    // },
    // leave:function(el,done){
    //   el.style.overflow="hidden";
    //   el.style.height="0px";
    //   window.setTimeout(function(){el.parentNode.removeChild(el)},1000);
    // }
  },
  mounted:function(){
      console.log('fasdfasd')
      this.$router.push('/')
  }
}
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    height: 100%;
    background-color: rgba(92, 180, 142, 0.4);
  }

  #main {
    width: 70%;
    margin: 0 auto;
  }

  .el-header,
  .el-footer {
    color: #ddd;
    text-align: center;
    line-height: 60px;
  }

  .el-aside {
    color: white;
    text-align: center;
    line-height: 170px;
  }

  ul{
    width: 100%;
    margin-top: 30px;
  }

  li{
    width: 100%;
    margin-bottom: 30px;
  }
  
  .navLink{
    display: block;
    width: 170px;
    margin: 0 auto;
    text-decoration: none;
    color: white;
    height: 170px;
  }

  .el-main {
    color: #333;
    text-align: left;
    padding-top: 100px;
  }

  #avatar {
    display: inline-block;
    text-align: left;
  }

  #avatar > img{
    display: block;
    width: 100px;
    height: 100px; 
  }

  .avatarAnimation{
    animation: avatar 2s forwards;
  }

  .avatarReverse{
    animation: avatarReverse 2s forwards;
  }

  @keyframes avatar{
    0% {padding-left:0;padding-top:0}
    60% {padding-left:50%;margin-left: -50px;padding-top:0px;}
    100% {padding-left:50%;margin-left: -50px;padding-top:100px;}
  }
   @keyframes avatarReverse{
    100% {padding-left:0;padding-top:0}
    60% {padding-left:50%;margin-left: -50px;padding-top:0px;}
    0% {padding-left:50%;margin-left: -50px;padding-top:100px;}
  }

  #app>.el-container {
    height: 100%;
  }


  .main-enter-active, .main-leave-active{
    transition: all 0.5s
  }
  .main-enter, .main-leave-to{
    opacity:0;
  }
</style>



